import {createFeatureSelector, createSelector} from '@ngrx/store';

import * as fromCommunication from '@communication/reducers/reducer';

export const selectCommunications = createFeatureSelector<fromCommunication.State>('communication');

export const selectCommunicationTypes = createSelector(
    selectCommunications,
    (state: fromCommunication.State) => state.communicationTypes
);

export const selectCommunicationData = createSelector(
    selectCommunications,
    (state: fromCommunication.State) => state.communications
);

export const selectCommunicationWaveTypes = createSelector(
    selectCommunications,
    (state: fromCommunication.State) => state.waveTypeList
);

export const selectCommunicationErrors = createSelector(
    selectCommunications,
    (state: fromCommunication.State) => state.errors
);
