import {Action} from '@ngrx/store';
import {ICourse} from '@models/interfaces/courses';

export enum ActionTypes {
    LoadCourses = '[Courses Page] Load Courses',
    LoadedCourses = '[Courses Page] Loaded Courses',
    RefreshCourses = '[Courses Page] Refresh Courses',
    CreateCourse = '[Courses Page] Create Course',
    UpdateCourse = '[Course Page] Update Course',
    DeleteCourse = '[Courses Page] Delete Course',
    CoursesErrors = '[Courses Page] Courses Errors',
    LoadCoursesByDivision = '[Course Page] Load Courses By Division',
    LoadCoursesByPerson = '[Course Page] Load Courses By Person',
    LoadedCoursesCount = '[Course Page] Loaded Courses Count'
}

export class LoadCourses implements Action {
    readonly type = ActionTypes.LoadCourses;
}

export class LoadedCourses implements Action {
    readonly type = ActionTypes.LoadedCourses;

    constructor(public payload: ICourse[]) {
    }
}

export class RefreshCourses implements Action {
    readonly type = ActionTypes.RefreshCourses;

    constructor(public payload: ICourse[]) {
    }
}

export class CreateCourse implements Action {
    readonly type = ActionTypes.CreateCourse;

    constructor(public payload: ICourse) {
    }
}

export class UpdateCourse implements Action {
    readonly type = ActionTypes.UpdateCourse;

    constructor(public payload: ICourse) {
    }
}

export class DeleteCourse implements Action {
    readonly type = ActionTypes.DeleteCourse;

    constructor(public payload: string) {
    }
}

export class CoursesErrors implements Action {
    readonly type = ActionTypes.CoursesErrors;

    constructor(public payload: any) {
    }
}

export class LoadCoursesByDivision implements Action {
    readonly type = ActionTypes.LoadCoursesByDivision;

    constructor(public payload: { pageNumber: number, pageSize: number, divisionId: string }) {
    }
}

export class LoadCoursesByPerson implements Action {
    readonly type = ActionTypes.LoadCoursesByPerson;

    constructor(public payload: { pageNumber: number, pageSize: number, personId: string }) {
    }
}

export class LoadedCoursesCount implements Action {
    readonly type = ActionTypes.LoadedCoursesCount;

    constructor(public payload: number) {
    }
}


export type CoursesActions = LoadCourses &
    LoadedCourses & RefreshCourses &
    CreateCourse & DeleteCourse &
    CoursesErrors & UpdateCourse &
    LoadCoursesByDivision & LoadCoursesByPerson &
    LoadedCoursesCount;
