import {Action} from '@ngrx/store';
import {ICommunication, ICommunicationType} from '@models/interfaces/communication';

export enum ActionTypes {
    LoadCommunicationTypes = '[Communication Types] Load Communication Types',
    LoadedCommunicationTypes = '[Communication Types] Loaded Communication Types',
    CreateCommunicationType = '[Communication Types] Create Communication Type',
    UpdateCommunicationType = '[Communication Types] Update Communication Type',
    DeleteCommunicationType = '[Communication Types] Delete Communication Type',
    LoadWaveTypeList = '[Communication Types] Load Wave Type List',
    LoadedWaveTypeList = '[Communication Types] Loaded Wave Type List',
    LoadCommunications = '[Communication Page] Load Communications',
    LoadedCommunications = '[Communication Page] Loaded Communications',
    CreateCommunication = '[Communication Page] Create Communication',
    DeleteCommunication = '[Communication Page] Delete Communication',
    LinkCommunication = '[Communication Page] Link Communication',
    UnlinkCommunication = '[Communication Page] Unlink Communication',
    CommunicationErrors = '[Communication] Communication Errors'
}

export class LoadCommunicationTypes implements Action {
    readonly type = ActionTypes.LoadCommunicationTypes;
}

export class LoadedCommunicationTypes implements Action {
    readonly type = ActionTypes.LoadedCommunicationTypes;

    constructor(public payload: ICommunicationType[]) {
    }
}

export class CreateCommunicationType implements Action {
    readonly type = ActionTypes.CreateCommunicationType;

    constructor(public payload: ICommunicationType) {
    }
}

export class UpdateCommunicationType implements Action {
    readonly type = ActionTypes.UpdateCommunicationType;

    constructor(public payload: ICommunicationType) {
    }
}

export class DeleteCommunicationType implements Action {
    readonly type = ActionTypes.DeleteCommunicationType;

    constructor(public payload: string) {
    }
}

export class CommunicationErrors implements Action {
    readonly type = ActionTypes.CommunicationErrors;

    constructor(public payload: any) {
    }
}

export class LoadWaveTypeList implements Action {
    readonly type = ActionTypes.LoadWaveTypeList;
}

export class LoadedWaveTypeList implements Action {
    readonly type = ActionTypes.LoadedWaveTypeList;

    constructor(public payload: string[]) {
    }
}

export class LoadCommunications implements Action {
    readonly type = ActionTypes.LoadCommunications;
}

export class LoadedCommunications implements Action {
    readonly type = ActionTypes.LoadedCommunications;

    constructor(public payload: ICommunication[]) {
    }
}

export class CreateCommunication implements Action {
    readonly type = ActionTypes.CreateCommunication;

    constructor(public payload: ICommunication) {
    }
}

export class DeleteCommunication implements Action {
    readonly type = ActionTypes.DeleteCommunication;

    constructor(public payload: string) {
    }
}

export class LinkCommunication implements Action {
    readonly type = ActionTypes.LinkCommunication;

    constructor(public payload: {communicationId: string, personId: string}) {
    }
}

export class UnlinkCommunication implements Action {
    readonly type = ActionTypes.UnlinkCommunication;

    constructor(public payload: string) {
    }
}

export type CommunicationActions = LoadCommunicationTypes &
    LoadedCommunicationTypes & CreateCommunicationType &
    UpdateCommunicationType & DeleteCommunicationType &
    CommunicationErrors & LoadWaveTypeList & LoadedWaveTypeList &
    LoadCommunications & LoadedCommunications & CreateCommunication &
    DeleteCommunication & LinkCommunication & UnlinkCommunication;
