import {ICommunication, ICommunicationType} from '@models/interfaces/communication';
import {ActionTypes, CommunicationActions} from '@communication/actions/actions';

export interface State {
    communicationTypes: ICommunicationType[];
    waveTypeList: string[];
    communications: ICommunication[];
    errors?: any;
}

export const initialState: State = {
    communicationTypes: undefined,
    waveTypeList: undefined,
    errors: undefined,
    communications: undefined
};

export function reducer(state: State = initialState, action: CommunicationActions) {
    switch (action.type) {
        case ActionTypes.LoadedCommunicationTypes:
            return {...state, communicationTypes: action.payload};
        case ActionTypes.LoadedWaveTypeList:
            return {...state, waveTypeList: action.payload};
        case ActionTypes.LoadedCommunications:
            return {...state, communications: action.payload};
        case ActionTypes.CommunicationErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
