import {IWeapon} from '@models/interfaces/weapons';

export const weaponMapperToServer = (data: IWeapon) => {
    return {
        count: data.count,
        owner: data.owner.id,
        serialNumber: data.serialNumber,
        weaponType: data.weaponName.id
    };
};

export const weaponMapperFromServer = (data: IWeapon): IWeapon => {
    return {
        serialNumber: data.serialNumber,
        weaponName: data.weaponName,
        owner: data.owner || undefined,
        count: data.count,
        id: data.id
    };
};
