import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {ICommunication, ICommunicationType} from '@models/interfaces/communication';
import {communicationMapperFromServer, communicationMapperToServer, communicationTypeMapperToServer} from '@mappers/communication.mapper';

@Injectable({
    providedIn: 'root'
})
export class CommunicationService {
    public baseUrl: string = environment.BASE_URL + '/api/commequipmenttype/' + environment.version;
    public communicationUrl: string = environment.BASE_URL + '/api/communicationequipment/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getCommunicationTypes(): Observable<ICommunicationType[]> {
        return this.http.get<ICommunicationType[]>(`${this.baseUrl}/get/all`)
            .pipe(map((response: ICommunicationType[]) =>
                response.map((item: ICommunicationType) => communicationTypeMapperToServer(item))));
    }

    createCommunicationType(body: ICommunicationType): Observable<ICommunicationType> {
        const mappedBody = communicationTypeMapperToServer(body);
        return this.http.post<ICommunicationType>(`${this.baseUrl}/create`, mappedBody)
            .pipe(map((response: ICommunicationType) => response));
    }

    updateCommunicationType(type: ICommunicationType): Observable<ICommunicationType> {
        const mappedBody = communicationTypeMapperToServer(type);
        return this.http.put<ICommunicationType>(`${this.baseUrl}/update/${type.id}`, mappedBody)
            .pipe(map((response: ICommunicationType) => communicationTypeMapperToServer(response)));
    }

    deleteCommunicationType(typeId: string): Observable<ICommunicationType> {
        return this.http.delete<ICommunicationType>(`${this.baseUrl}/delete/${typeId}`)
            .pipe(map((response) => response));
    }

    getWaveTypeList(): Observable<string[]> {
        return this.http.get<string[]>(`${this.baseUrl}/enum/type`)
            .pipe(map((response: string[]) => response));
    }

    getCommunications(): Observable<ICommunication[]> {
        return this.http.get<ICommunication[]>(`${this.communicationUrl}/all`)
            .pipe(map((response: ICommunication[]) => response.map((item: ICommunication) => communicationMapperFromServer(item))));
    }

    createCommunication(communication: ICommunication): Observable<ICommunication> {
        const mappedBody = communicationMapperToServer(communication);
        return this.http.post<ICommunication>(`${this.communicationUrl}/create`, mappedBody)
            .pipe(map((response: ICommunication) => communicationMapperFromServer(response)));
    }

    deleteCommunication(communicationId: string): Observable<any> {
        return this.http.delete<ICommunication>(`${this.communicationUrl}/delete/${communicationId}`)
            .pipe(map((response: any) => response));
    }

    linkCommunication(communicationId: string, personId: string): Observable<ICommunication> {
        return this.http.post<ICommunication>(`${this.communicationUrl}/${communicationId}/${personId}`, null)
            .pipe(map((response: ICommunication) => communicationMapperFromServer(response)));
    }

    unlinkCommunication(communicationId: string): Observable<ICommunication> {
        return this.http.post<ICommunication>(`${this.communicationUrl}/${communicationId}/remove`, null)
            .pipe(map((response: ICommunication) => communicationMapperFromServer(response)));
    }
}
