import {IPerson} from '@models/interfaces/human';

export const personMapperToServer = (data: IPerson): any => {
    return {
        id: data.id,
        active: data.active,
        roleName: data.roleName,
        userName: data.userName,
        birthDate: data.birthDate,
        rank: data.rank.id,
        division: data.division.id,
        level: data.level,
        call: data.call,
        rfidCode: data.rfidCode,
        number: data.number,
        address: data.address,
    };
};

export const personMapperFromServer = (data: any): IPerson => {
    return {
        id: data.id,
        userName: data.userName,
        birthDate: data.birthDate,
        rank: data.rank,
        division: data.division,
        level: data.level,
        active: data.active,
        member: false,
        roleName: data.roleName,
        call: data.call || '-',
        rfidCode: data.rfidCode || '-',
        number: data.number || '-',
        address: data.address,
    };
};

export const checkinToServer = (data: IPerson) => {
    return {
        person: data.id,
        status: data.status
    };
};

