import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {of} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';

import * as fromCommunication from '@communication/actions/actions';
import {CommunicationService} from '@communication/services/communication.service';
import {MessageService} from '@services/message.service';
import {ICommunication, ICommunicationType} from '@models/interfaces/communication';
import {selectCommunicationData, selectCommunicationTypes} from '@communication/selectors/selectors';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';


@Injectable()
export class CommunicationEffects {
    constructor(private actions$: Actions,
                private communicationService: CommunicationService,
                private messageService: MessageService,
                private store: Store<any>) {
    }

    @Effect()
    loadCommunicationTypes$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.LoadCommunicationTypes),
        mergeMap(() => this.communicationService.getCommunicationTypes()
            .pipe(
                map((communicationTypes: ICommunicationType[]) => new fromCommunication.LoadedCommunicationTypes(communicationTypes)),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            )
        )
    );

    @Effect()
    createCommunicationType$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.CreateCommunicationType),
        withLatestFrom(this.store.pipe(select(selectCommunicationTypes))),
        mergeMap(([action, types]: any[]) => this.communicationService.createCommunicationType(action.payload)
            .pipe(
                map((type: ICommunicationType) => {
                    const message = {user: ToastrUsersType.COMMUNICATION_TYPE, type: ToastrMessageType.CREATE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types.push(type);
                    }
                    return new fromCommunication.LoadedCommunicationTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            ))
    );

    @Effect()
    updateCommunicationType$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.UpdateCommunicationType),
        withLatestFrom(this.store.pipe(select(selectCommunicationTypes))),
        mergeMap(([action, types]: any[]) => this.communicationService.updateCommunicationType(action.payload)
            .pipe(
                map((type: ICommunicationType) => {
                    const message = {user: ToastrUsersType.COMMUNICATION_TYPE, type: ToastrMessageType.UPDATE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types = types.map((item: ICommunicationType) => item.id === type.id ? type : item);
                    }
                    return new fromCommunication.LoadedCommunicationTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            ))
    );

    @Effect()
    deleteCommunicationType$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.DeleteCommunicationType),
        withLatestFrom(this.store.pipe(select(selectCommunicationTypes))),
        mergeMap(([action, types]: any[]) => this.communicationService.deleteCommunicationType(action.payload)
            .pipe(
                map((type: ICommunicationType) => {
                    const message = {user: ToastrUsersType.COMMUNICATION_TYPE, type: ToastrMessageType.REMOVE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types = types.filter((item: ICommunicationType) => item.id !== action.payload);
                    }
                    return new fromCommunication.LoadedCommunicationTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            ))
    );

    @Effect()
    loadWaveTypes = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.LoadWaveTypeList),
        mergeMap(() => this.communicationService.getWaveTypeList()
            .pipe(
                map((list: string[]) => new fromCommunication.LoadedWaveTypeList(list)),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            )
        )
    );

    @Effect()
    loadCommunications$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.LoadCommunications),
        mergeMap(() => this.communicationService.getCommunications()
            .pipe(
                map((communications: ICommunication[]) => new fromCommunication.LoadedCommunications(communications)),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            )
        )
    );

    @Effect()
    createCommunication$ = this.actions$
        .pipe(
            ofType(fromCommunication.ActionTypes.CreateCommunication),
            withLatestFrom(this.store.select(selectCommunicationData)),
            switchMap(([action, communications]: any[]) => this.communicationService.createCommunication(action.payload)
                .pipe(
                    map((communication: ICommunication) => {
                        const isUpdated = communications.find((item: ICommunication) => item.id === communication.id);
                        if (isUpdated) {
                            const message = {user: ToastrUsersType.COMMUNICATION, type: ToastrMessageType.UPDATE};
                            communications = communications.map((item: ICommunication) =>
                                item.id === communication.id ? communication : item);
                            this.messageService.showToastrSuccess(message);
                        } else {
                            const message = {user: ToastrUsersType.COMMUNICATION, type: ToastrMessageType.CREATE};
                            communications.push(communication);
                            this.messageService.showToastrSuccess(message);
                        }

                        return new fromCommunication.LoadedCommunications(communications);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromCommunication.CommunicationErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteCommunication$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.DeleteCommunication),
        withLatestFrom(this.store.pipe(select(selectCommunicationData))),
        mergeMap(([action, communications]: any[]) => this.communicationService.deleteCommunication(action.payload)
            .pipe(
                map(() => {
                    const message = {user: ToastrUsersType.COMMUNICATION, type: ToastrMessageType.REMOVE};
                    this.messageService.showToastrSuccess(message);
                    communications = communications.filter((item: ICommunicationType) => item.id !== action.payload);
                    return new fromCommunication.LoadedCommunications(communications);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            ))
    );

    @Effect()
    linkCommunication$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.LinkCommunication),
        mergeMap((action: any) => {
            const {communicationId, personId} = action.payload;
            return this.communicationService.linkCommunication(communicationId, personId).pipe(
                map(() => new fromCommunication.LoadCommunications()),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            );
        })
    );

    @Effect()
    unlinkCommunication$ = this.actions$.pipe(
        ofType(fromCommunication.ActionTypes.UnlinkCommunication),
        mergeMap((action: any) => this.communicationService.unlinkCommunication(action.payload)
            .pipe(
                map(() => new fromCommunication.LoadCommunications()),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromCommunication.CommunicationErrors(err));
                })
            )
        )
    );
}
