import {IMachine, IMachineType} from '@models/interfaces/machine';
import {ActionTypes, MachineActions} from '@machine/actions/actions';

export interface State {
    machineTypes: IMachineType[];
    machines: IMachine[];
    errors?: any;
}

export const initialState: State = {
    machineTypes: undefined,
    machines: undefined,
    errors: undefined
};

export function reducer(state: State = initialState, action: MachineActions) {
    switch (action.type) {
        case ActionTypes.LoadedMachineTypes:
            return {...state, machineTypes: action.payload};
        case ActionTypes.LoadedMachines:
            return {...state, machines: action.payload};
        case ActionTypes.MachineErrors:
            return {...state, errors: action.payload};
        default:
            return state;
    }
}
