import {Component, Input, OnInit} from '@angular/core';
import {IPerson} from '@models/interfaces/human';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ISubject, ITest, ITestReport} from '@models/interfaces/quiz';

@Component({
    selector: 'app-check-modal',
    templateUrl: './check-modal.component.html',
    styleUrls: ['./check-modal.component.scss']
})
export class CheckModalComponent implements OnInit {
    public language: string;
    @Input() person: IPerson;
    @Input() test: ITest;
    @Input() report: ITestReport;
    @Input() isCheck: boolean;
    @Input() subject: ISubject;

    constructor(public activeModal: NgbActiveModal) {
    }

    ngOnInit() {
        this.language = localStorage.getItem('lang');
    }

}
