import {Component, OnInit} from '@angular/core';
import {tap} from 'rxjs/operators';
import {Observable} from 'rxjs';
import {select, Store} from '@ngrx/store';

import {CommunicationMocks} from '@models/mocks/communication';
import {Actions} from '@models/constants/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {WeaponModalComponent} from '@modal/components/weapon-modal/weapon-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {PageTypes} from '@models/constants/page-types';
import {IMachine} from '@models/interfaces/machine';
import {CreateMachine, DeleteMachine, LinkMachine, LoadMachines, LoadMachineTypes, UnlinkMachine} from '@machine/actions/actions';
import {selectMachinesData} from '@machine/selectors/selectors';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-machine',
    templateUrl: './machine.component.html',
    styleUrls: ['./machine.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class MachineComponent implements OnInit {
    public pageTypes = PageTypes;
    public actions: string[];

    public machines$: Observable<IMachine[]>;

    constructor(private modalService: ModalService,
                private store: Store<any>) {
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE, Actions.TIE, Actions.UNTIE];
        this.getMachines();
        this.getMachineTypes();
    }

    getMachines() {
        this.machines$ = this.store.pipe(
            select(selectMachinesData),
            tap((machines: IMachine[]) => !machines && this.fetchMachines())
        );
    }

    fetchMachines() {
        this.store.dispatch(new LoadMachines());
    }

    getMachineTypes() {
        this.store.dispatch(new LoadMachineTypes());
    }

    add() {
        this.openMachineModal(CommunicationMocks.emptyMachine, false);
    }

    handleActions(event: { action: string, item: IMachine }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openMachineModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.TIE:
                return this.openLinkModal(event.item.id, {owner: undefined}, true);
            case Actions.UNTIE:
                return this.unlinkMachine(event.item.id);
        }
    }

    openMachineModal(machine: IMachine, edit: boolean) {
        const objectType = this.pageTypes.MACHINE;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...machine}, edit, objectType},
            (res) => {
                if (res) {
                    if (edit) {
                        const body = {...machine, ...res};
                        this.createMachine(body);
                    } else {
                        this.createMachine(res);
                    }
                }
            });
    }

    openDialogModal(message: string, item: IMachine) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.serialNumber, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteMachine(item.id);
                }
            });
    }

    openLinkModal(machineId: string, object: any, isLink: boolean) {
        this.modalService.openModal(WeaponModalComponent,
            {centered: true},
            {object, isLink},
            (res) => {
                if (res) {
                    this.linkMachine(machineId, res.owner.id);
                }
            }
        );
    }

    createMachine(machine: IMachine) {
        this.store.dispatch(new CreateMachine(machine));
    }

    deleteMachine(machineId: string) {
        this.store.dispatch(new DeleteMachine(machineId));
    }

    linkMachine(machineId: string, personId: string) {
        this.store.dispatch(new LinkMachine({machineId, personId}));
    }

    unlinkMachine(machineId: string) {
        this.store.dispatch(new UnlinkMachine(machineId));
    }

}
