import {Action} from '@ngrx/store';
import {IWorkSpace} from '@models/interfaces/workspace';

export enum ActionTypes {
    LoadWorkspaces = '[Workspace Page] Load Workspaces',
    LoadedWorkspaces = '[Workspace Page] Loaded Workspaces',
    LoadCurrentWorkspace = '[Workspace Page] Load Current Workspace',
    LoadedCurrentWorkspace = '[Workspace Page] Loaded Current Workspace',
    SendCurrentWorkspace = '[Workspace Page] Send Current Workspace',
    CheckWorkspaces = '[Workspace Page] Check Workspaces',
    LoadCheckStatus = '[Answers Page] Load Check Status',
    LoadedCheckStatus = '[Answers Page] Loaded Check Status',
    StartWorkspaces = '[Workspace Page] Start Workspaces'
}

export class LoadWorkspaces implements Action {
    readonly type = ActionTypes.LoadWorkspaces;
}

export class LoadedWorkspaces implements Action {
    readonly type = ActionTypes.LoadedWorkspaces;

    constructor(public payload: IWorkSpace[]) {
    }
}

export class LoadCurrentWorkspace implements Action {
    readonly type = ActionTypes.LoadCurrentWorkspace;
}

export class LoadedCurrentWorkspace implements Action {
    readonly type = ActionTypes.LoadedCurrentWorkspace;

    constructor(public payload: IWorkSpace) {
    }
}

export class SendCurrentWorkspace implements Action {
    readonly type = ActionTypes.SendCurrentWorkspace;

    constructor(public payload: IWorkSpace) {
    }
}

export class CheckWorkspaces implements Action {
    readonly type = ActionTypes.CheckWorkspaces;

    constructor(public payload: IWorkSpace[]) {
    }
}

export class StartWorkspaces implements Action {
    readonly type = ActionTypes.StartWorkspaces;

    constructor(public payload: IWorkSpace[]) {
    }
}

export class LoadCheckStatus implements Action {
    readonly type = ActionTypes.LoadCheckStatus;

    constructor(public payload: boolean) {
    }
}

export class LoadedCheckStatus implements Action {
    readonly type = ActionTypes.LoadedCheckStatus;

    constructor(public payload: boolean) {
    }
}

export type WorkspaceActions = LoadWorkspaces &
    LoadedWorkspaces &
    LoadCurrentWorkspace &
    LoadedCurrentWorkspace &
    SendCurrentWorkspace &
    CheckWorkspaces &
    LoadCheckStatus &
    LoadedCheckStatus &
    StartWorkspaces;
