import {ICommunication, ICommunicationType} from '@models/interfaces/communication';
import {IMachine} from '@models/interfaces/machine';

const translateWaveType = (waveType) => {
    switch (waveType) {
        case 'SHORT_WAVE':
            return '';
        case 'ULRTA_SHORT_WAVE':
            return '';
        case '':
            return 'SHORT_WAVE';
        case '':
            return 'ULRTA_SHORT_WAVE';
    }
};
export const communicationTypeMapperToServer = (data: ICommunicationType): ICommunicationType => {
    return {
        id: data.id,
        name: data.name,
        type: translateWaveType(data.type)
    };
};

export const communicationMapperToServer = (data: ICommunication) => {
    return {
        owner: data.owner.id,
        serialNumber: data.serialNumber,
        type: data.communicationType.id
    };
};

export const communicationMapperFromServer = (data: any): ICommunication => {
    return {
        owner: data.owner,
        serialNumber: data.serialNumber,
        communicationType: data.type,
        id: data.id
    };
};

export const machineMapperToServer = (data: IMachine) => {
    return {
        owner: data.owner.id,
        serialNumber: data.serialNumber,
        passportNumber: data.passportNumber,
        type: data.machineType.id,
        count: data.fuelCount
    };
};

export const machineMapperFromServer = (data: any): IMachine => {
    return {
        owner: data.owner,
        passportNumber: data.passportNumber,
        serialNumber: data.serialNumber,
        machineType: data.type,
        fuelCount: data.count,
        id: data.id
    };
};
