import {createFeatureSelector, createSelector} from '@ngrx/store';

import * as fromMachine from '@machine/reducers/reducer';

export const selectMachines = createFeatureSelector<fromMachine.State>('machine');

export const selectMachineTypes = createSelector(
    selectMachines,
    (state: fromMachine.State) => state.machineTypes
);

export const selectMachinesData = createSelector(
    selectMachines,
    (state: fromMachine.State) => state.machines
);

export const selectMachineErrors = createSelector(
    selectMachines,
    (state: fromMachine.State) => state.errors
);
