import {Component, OnInit} from '@angular/core';
import {PageTypes} from '@models/constants/page-types';
import {Observable} from 'rxjs';
import {IMachineType} from '@models/interfaces/machine';
import {select, Store} from '@ngrx/store';
import {ModalService} from '@modal/services/modal.service';
import {Actions} from '@models/constants/actions';
import {selectMachineTypes} from '@machine/selectors/selectors';
import {tap} from 'rxjs/operators';
import {
    CreateMachineType,
    DeleteMachineType,
    LoadMachineTypes,
    UpdateMachineType
} from '@machine/actions/actions';
import {CommunicationMocks} from '@models/mocks/communication';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {Animations} from '@models/constants/animations';

@Component({
    selector: 'app-machine-type',
    templateUrl: './machine-type.component.html',
    styleUrls: ['./machine-type.component.scss'],
    animations: [Animations.enterLeaveOpacity]

})
export class MachineTypeComponent implements OnInit {

    public actions: string[];
    public pageTypes = PageTypes;
    public machineTypes$: Observable<IMachineType[]>;

    constructor(private store: Store<any>,
                private modalService: ModalService) {
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getMachineTypes();
    }

    getMachineTypes() {
        this.machineTypes$ = this.store.pipe(
            select(selectMachineTypes),
            tap((types: IMachineType[]) => !types && this.fetchMachineTypes())
        );
    }

    fetchMachineTypes() {
        this.store.dispatch(new LoadMachineTypes());
    }

    handleActions(event: { action: string, item: IMachineType }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openMachineTypeModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return;
        }
    }

    add() {
        this.openMachineTypeModal(CommunicationMocks.emptyMachineTypes, false);
    }

    openMachineTypeModal(type: IMachineType, edit: boolean) {
        const objectType = this.pageTypes.MACHINE_TYPES;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...type}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateMachineType(type, res) : this.createMachineType(res);
                }
            });
    }

    openDialogModal(message: string, item: IMachineType) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteMachineType(item.id);
                }
            });
    }

    createMachineType(type: IMachineType) {
        this.store.dispatch(new CreateMachineType(type));
    }

    updateMachineType(type: IMachineType, updatedType: IMachineType) {
        updatedType.id = type.id;
        this.store.dispatch(new UpdateMachineType(updatedType));
    }

    deleteMachineType(typeId: string) {
        this.store.dispatch(new DeleteMachineType(typeId));
    }

}
