import {Component, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

import {ICommunicationType} from '@models/interfaces/communication';
import {
    CreateCommunicationType,
    DeleteCommunicationType,
    LoadCommunicationTypes,
    UpdateCommunicationType
} from '@communication/actions/actions';
import {selectCommunicationTypes} from '@communication/selectors/selectors';
import {Animations} from '@models/constants/animations';
import {Actions} from '@models/constants/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {PageTypes} from '@models/constants/page-types';
import {ModalService} from '@modal/services/modal.service';
import {CommunicationMocks} from '@models/mocks/communication';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';

@Component({
    selector: 'app-communication-type',
    templateUrl: './communication-type.component.html',
    styleUrls: ['./communication-type.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class CommunicationTypeComponent implements OnInit {
    public actions: string[];
    public pageTypes = PageTypes;
    public communicationTypes$: Observable<ICommunicationType[]>;

    constructor(private store: Store<any>,
                private modalService: ModalService) {
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getCommunicationTypes();
    }

    getCommunicationTypes() {
        this.communicationTypes$ = this.store.pipe(
            select(selectCommunicationTypes),
            tap((types: ICommunicationType[]) => !types && this.fetchCommunicationTypes())
        );
    }

    fetchCommunicationTypes() {
        this.store.dispatch(new LoadCommunicationTypes());
    }

    handleActions(event: { action: string, item: ICommunicationType }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openCommunicationTypeModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            default:
                return;
        }
    }

    add() {
        this.openCommunicationTypeModal(CommunicationMocks.emptyCommunicationTypes, false);
    }

    openCommunicationTypeModal(type: ICommunicationType, edit: boolean) {
        const objectType = this.pageTypes.COMMUNICATION_TYPES;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...type}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateCommunicationType(type, res) : this.createCommunicationType(res);
                }
            });
    }

    openDialogModal(message: string, item: ICommunicationType) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteCommunicationType(item.id);
                }
            });
    }

    createCommunicationType(type: ICommunicationType) {
        this.store.dispatch(new CreateCommunicationType(type));
    }

    updateCommunicationType(type: ICommunicationType, updatedType: ICommunicationType) {
        updatedType.id = type.id;
        this.store.dispatch(new UpdateCommunicationType(updatedType));
    }

    deleteCommunicationType(typeId: string) {
        this.store.dispatch(new DeleteCommunicationType(typeId));
    }

}
