import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';

import {environment} from '@environments/environment';
import {Observable} from 'rxjs';
import {IMachineType} from '@models/interfaces/machine';
import {IMachine} from '@models/interfaces/machine';
import {machineMapperFromServer, machineMapperToServer} from '@mappers/communication.mapper';

@Injectable({
    providedIn: 'root'
})
export class MachineService {
    public baseUrl: string = environment.BASE_URL + '/api/vehicletype/' + environment.version;
    public machineUrl: string = environment.BASE_URL + '/api/vehicle/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getMachineTypes(): Observable<IMachineType[]> {
        return this.http.get<IMachineType[]>(`${this.baseUrl}/get/all`)
            .pipe(map((response: IMachineType[]) => response));
    }

    createMachineType(body: string): Observable<IMachineType> {
        return this.http.post<IMachineType>(`${this.baseUrl}/create`, body)
            .pipe(map((response: IMachineType) => response));
    }

    updateMachineType(type: IMachineType): Observable<IMachineType> {
        return this.http.put<IMachineType>(`${this.baseUrl}/update/${type.id}`, type)
            .pipe(map((response: IMachineType) => response));
    }

    deleteMachineType(typeId: string): Observable<IMachineType> {
        return this.http.delete<IMachineType>(`${this.baseUrl}/delete/${typeId}`)
            .pipe(map((response) => response));
    }

    getMachines(): Observable<IMachine[]> {
        return this.http.get<IMachine[]>(`${this.machineUrl}/all`)
            .pipe(map((response: IMachine[]) => response.map((item: IMachine) => machineMapperFromServer(item))));
    }

    createMachine(machine: IMachine): Observable<IMachine> {
        const mappedBody = machineMapperToServer(machine);
        return this.http.post<IMachine>(`${this.machineUrl}/create`, mappedBody)
            .pipe(map((response: IMachine) => machineMapperFromServer(response)));
    }

    deleteMachine(machineId: string): Observable<any> {
        return this.http.delete<IMachine>(`${this.machineUrl}/delete/${machineId}`)
            .pipe(map((response: any) => response));
    }

    linkMachine(machineId: string, personId: string): Observable<IMachine> {
        return this.http.post<IMachine>(`${this.machineUrl}/${machineId}/${personId}`, null)
            .pipe(map((response: IMachine) => machineMapperFromServer(response)));
    }

    unlinkMachine(machineId: string): Observable<IMachine> {
        return this.http.post<IMachine>(`${this.machineUrl}/${machineId}/remove`, null)
            .pipe(map((response: IMachine) => machineMapperFromServer(response)));
    }
}
