import {Action} from '@ngrx/store';
import {IMachineType} from '@models/interfaces/machine';
import {IMachine} from '@models/interfaces/machine';

export enum ActionTypes {
    LoadMachineTypes = '[Machine Types] Load Machine Types',
    LoadedMachineTypes = '[Machine Types] Loaded Machine Types',
    CreateMachineType = '[Machine Types] Create Machine Type',
    UpdateMachineType = '[Machine Types] Update Machine Type',
    DeleteMachineType = '[Machine Types] Delete Machine Type',
    LoadMachines = '[Machine Page] Load Machines',
    LoadedMachines = '[Machine Page] Loaded Machines',
    CreateMachine = '[Machine Page] Create Machine',
    DeleteMachine = '[Machine Page] Delete Machine',
    LinkMachine = '[Machine Page] Link Machine',
    UnlinkMachine = '[Machine Page] Unlink Machine',
    MachineErrors = '[Machine] Machine Errors'
}

export class LoadMachineTypes implements Action {
    readonly type = ActionTypes.LoadMachineTypes;
}

export class LoadedMachineTypes implements Action {
    readonly type = ActionTypes.LoadedMachineTypes;

    constructor(public payload: IMachineType[]) {
    }
}

export class CreateMachineType implements Action {
    readonly type = ActionTypes.CreateMachineType;

    constructor(public payload: IMachineType) {
    }
}

export class UpdateMachineType implements Action {
    readonly type = ActionTypes.UpdateMachineType;

    constructor(public payload: IMachineType) {
    }
}

export class DeleteMachineType implements Action {
    readonly type = ActionTypes.DeleteMachineType;

    constructor(public payload: string) {
    }
}

export class MachineErrors implements Action {
    readonly type = ActionTypes.MachineErrors;

    constructor(public payload: any) {
    }
}

export class LoadMachines implements Action {
    readonly type = ActionTypes.LoadMachines;
}

export class LoadedMachines implements Action {
    readonly type = ActionTypes.LoadedMachines;

    constructor(public payload: IMachine[]) {
    }
}

export class CreateMachine implements Action {
    readonly type = ActionTypes.CreateMachine;

    constructor(public payload: IMachine) {
    }
}

export class DeleteMachine implements Action {
    readonly type = ActionTypes.DeleteMachine;

    constructor(public payload: string) {
    }
}

export class LinkMachine implements Action {
    readonly type = ActionTypes.LinkMachine;

    constructor(public payload: {machineId: string, personId: string}) {
    }
}

export class UnlinkMachine implements Action {
    readonly type = ActionTypes.UnlinkMachine;

    constructor(public payload: string) {
    }
}

export type MachineActions = LoadMachineTypes &
    LoadedMachineTypes & CreateMachineType &
    UpdateMachineType & DeleteMachineType &
    MachineErrors & LoadMachines & LoadedMachines &
    CreateMachine & DeleteMachine & LinkMachine &
    UnlinkMachine;
