import {Component, OnInit} from '@angular/core';
import {Observable, timer} from 'rxjs';
import {map, take, tap} from 'rxjs/operators';
import {ActivatedRoute} from '@angular/router';
import {select, Store} from '@ngrx/store';
import * as moment from 'moment';
import 'moment-duration-format';

import {UnsubscribeOndestroyAdapter} from '../../../../adstracts/unsubscribe-ondestroy-adapter';
import {IAnswers, IQuestion, ISubject, ITest, ITestReport, ITestResult} from '@models/interfaces/quiz';
import {IPerson} from '@models/interfaces/human';
import {selectReport, selectSubjects, selectSubjectTests, selectTestQuestions} from '@quiz/selectors/selectors';
import {selectPersonData} from '@persons/selectors/selectors';
import {CreateAnswersReport, GetQuestionsByTest, LoadSubjects, LoadTestsBySubject} from '@quiz/actions/actions';
import {LoadPersons} from '@persons/actions/actions';
import {SharedService} from '@shared/services/shared.service';
import {ModalService} from '@modal/services/modal.service';
import {CheckModalComponent} from '@modal/components/check-modal/check-modal.component';


@Component({
    selector: 'app-answers',
    templateUrl: './answers.component.html',
    styleUrls: ['./answers.component.scss']
})
export class AnswersComponent extends UnsubscribeOndestroyAdapter implements OnInit {
    public subjectId: string;
    public testId: string;
    public personId: string;
    public currentSubject: ISubject;
    public currentTest: ITest;
    public currentPerson: IPerson;
    public language: string;
    public currentQuestion: IQuestion;
    public questionIndex = 0;
    public shuffleAnswers: IAnswers[];
    public passedQuestions: ITestResult;
    public currentTestTime: number;
    public report: ITestReport;

    public subjects$: Observable<ISubject[]>;
    public tests$: Observable<ITest[]>;
    public persons$: Observable<IPerson[]>;
    public questions$: Observable<IQuestion[]>;

    constructor(private route: ActivatedRoute,
                private store: Store<any>,
                private sharedService: SharedService,
                private modalService: ModalService) {
        super();
    }

    static mapAnswers(answers: IAnswers[]): IAnswers[] {
        return answers.map((answer: IAnswers) => {
            return {
                kk: answer.kk,
                ru: answer.ru,
                imagePath: answer.imagePath,
                image: false
            };
        });
    }

    static getShuffleAnswers(answers: IAnswers[]): IAnswers[] {
        let currentIndex = answers.length;
        while (currentIndex !== 0) {
            const randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;
            const temporaryValue = answers[currentIndex];
            answers[currentIndex] = answers[randomIndex];
            answers[randomIndex] = temporaryValue;
        }
        return answers;
    }

    static getPassedList(personId: string, testId: string): ITestResult {
        return {
            list: [],
            person: personId,
            quizId: testId
        };
    }

    ngOnInit() {
        this.language = localStorage.getItem('lang');
        this.getRouteParams();
    }

    getRouteParams() {
        this.route.queryParamMap.subscribe((params) => {
            this.subjectId = params.get('subjectId');
            this.testId = params.get('quizId');
            this.personId = params.get('personId');
            this.getTests(this.subjectId, this.testId);
            this.getSubjects(this.subjectId);
            this.getPersons(this.personId);
            this.passedQuestions = AnswersComponent.getPassedList(this.personId, this.testId);
            this.getReport();
        });
    }

    getSubjects(subjectId: string) {
        this.subjects$ = this.store.pipe(
            select(selectSubjects),
            tap((subjects: ISubject[]) => {
                if (!subjects) {
                    return this.fetchSubjects();
                }
                this.currentSubject = this.findObjectById(subjects, subjectId);
            })
        );
    }

    fetchSubjects() {
        this.store.dispatch(new LoadSubjects());
    }

    getTests(subjectId: string, testId: string) {
        this.fetchTests(subjectId);
        this.tests$ = this.store.pipe(
            select(selectSubjectTests),
            tap((tests) => {
                if (tests) {
                    this.currentTest = this.findObjectById(tests, testId);
                    this.getQuestions(testId);
                    this.getTimer(this.currentTest);
                }
            })
        );
    }

    getTimer(test: ITest) {
        this.subscriptions.sink = timer(1000, 1000).pipe(
            map((i) => test.time - i),
            take(test.time + 1))
            .subscribe((time) => {
                this.currentTestTime = time;
                if (this.currentTestTime === 0) {
                    return this.store.dispatch(new CreateAnswersReport(this.passedQuestions));
                }
            });
    }

    fetchTests(subjectId: string) {
        this.store.dispatch(new LoadTestsBySubject(subjectId));
    }

    getPersons(personId: string) {
        this.persons$ = this.store.pipe(select(selectPersonData),
            tap((persons: IPerson[]) => {
                if (!persons) {
                    return this.fetchPersons();
                }
                this.currentPerson = this.findObjectById(persons, personId);
            }));
    }

    fetchPersons() {
        this.store.dispatch(new LoadPersons());
    }

    getQuestions(testId: string) {
        this.questions$ = this.store.pipe(
            select(selectTestQuestions),
            tap((questions: IQuestion[]) => {
                if (!questions) {
                    return this.fetchQuestions(testId);
                }
                this.getCurrentQuestion(questions, this.questionIndex);
            })
        );
    }

    fetchQuestions(testId: string) {
        this.store.dispatch(new GetQuestionsByTest(testId));
    }

    getNextQuestion(questions: IQuestion[], item: IAnswers) {
        const chosenAnswer = this.findAnswerIndex(questions, this.questionIndex, item);
        this.passedQuestions.list.push({answer: chosenAnswer + 1, questionId: this.currentQuestion.id});
        this.questionIndex += 1;
        this.getCurrentQuestion(questions, this.questionIndex);
    }

    getCurrentQuestion(questions: IQuestion[], index: number) {
        if (index > questions.length - 1) {
            return this.store.dispatch(new CreateAnswersReport(this.passedQuestions));
        }
        this.currentQuestion = questions[index];
        this.currentQuestion['image'] = false;
        this.currentQuestion.answers = AnswersComponent.mapAnswers(this.currentQuestion.answers);
        const {answers, random} = this.currentQuestion;
        if (random) {
            this.shuffleAnswers = AnswersComponent.getShuffleAnswers([...answers]);
        }
    }

    getReport() {
        this.subscriptions.sink = this.store.pipe(select(selectReport))
            .subscribe((report: ITestReport) => {
                if (report) {
                    this.openModal(report[0]);
                }
            });
    }

    openModal(report: ITestReport) {
        this.modalService.openModal(CheckModalComponent,
            {centered: true, backdrop: 'static'},
            {
                person: this.currentPerson,
                test: this.currentTest,
                subject: this.currentSubject,
                isCheck: false,
                report
            });
    }

    findAnswerIndex(questions: IQuestion[], index: number, item: IAnswers): number {
        return questions[index].answers.findIndex((answer: IAnswers) => answer === item);
    }


    findObjectById(array: any[], id: string): any {
        return array.find((item: any) => item.id === id);
    }

    get imageUrl() {
        return this.sharedService.imageBaseUrl;
    }

    formatTime(seconds: string): string {
        if (seconds.includes(':')) {
            return seconds;
        }
        const duration = moment.duration(Number(seconds), 'seconds');
        return duration.format('HH:mm:ss');
    }

    onImageClick(item: any) {
        item.image = !item.image;
    }

}
