import {Injectable} from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {Observable} from 'rxjs';

import {environment} from '@environments/environment';
import {ICourse} from '@models/interfaces/courses';

@Injectable({
    providedIn: 'root'
})
export class CoursesService {
    private coursesBaseUrl: string = environment.BASE_URL + '/api/course/' + environment.version;

    constructor(private http: HttpClient) {
    }

    getCourses(): Observable<ICourse[]> {
        return this.http.get(this.coursesBaseUrl + '/get/all/course')
            .pipe(map((response: ICourse[]) => response));
    }

    createCourse(course: ICourse): Observable<ICourse> {
        return this.http.post(this.coursesBaseUrl + '/post/course/', course)
            .pipe(map((response: ICourse) => response));
    }

    updateCourse(course: ICourse): Observable<ICourse> {
        return this.http.put(this.coursesBaseUrl + `/put/course/${course.id}`, course)
            .pipe(map((response: ICourse) => response));
    }

    deleteCourse(courseId: string): Observable<any> {
        return this.http.delete(this.coursesBaseUrl + `/delete/course/${courseId}`)
            .pipe(map((response) => response));
    }

    getCoursesByDivision(data: {pageNumber: number, pageSize: number, divisionId: string}): Observable<any> {
        const {pageNumber, pageSize, divisionId} = data;
        return this.http.get<any>(`${this.coursesBaseUrl}/division/${divisionId}/${pageNumber}/${pageSize}`, {observe: 'response'})
            .pipe(map((response: HttpResponse<ICourse[]>) => {
                const coursesCount: number = Number(response.headers.get('total'));
                return {courses: response.body, coursesCount};
            }));
    }

    getCoursesByPerson(data: {pageNumber: number, pageSize: number, personId: string}): Observable<any> {
        const {pageNumber, pageSize, personId} = data;
        return this.http.get<ICourse[]>(`${this.coursesBaseUrl}/person/${personId}/${pageNumber}/${pageSize}`, {observe: 'response'})
            .pipe(map((response: HttpResponse<ICourse[]>) => {
                const coursesCount: number = Number(response.headers.get('total'));
                return {courses: response.body, coursesCount};
            }));
    }
}
