import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {of} from 'rxjs';
import {select, Store} from '@ngrx/store';
import {catchError, map, mergeMap, switchMap, withLatestFrom} from 'rxjs/operators';

import * as fromMachine from '@machine/actions/actions';
import {MachineService} from '@machine/services/machine.service';
import {MessageService} from '@services/message.service';
import {IMachine, IMachineType} from '@models/interfaces/machine';
import {selectMachinesData, selectMachineTypes} from '@machine/selectors/selectors';
import {ToastrMessageType, ToastrUsersType} from '@models/constants/toastr';
import {ICommunication} from '@models/interfaces/communication';


@Injectable()
export class MachineEffects {
    constructor(private actions$: Actions,
                private machineService: MachineService,
                private messageService: MessageService,
                private store: Store<any>) {
    }

    @Effect()
    loadMachineTypes$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.LoadMachineTypes),
        mergeMap(() => this.machineService.getMachineTypes()
            .pipe(
                map((MachineTypes: IMachineType[]) => new fromMachine.LoadedMachineTypes(MachineTypes)),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            )
        )
    );

    @Effect()
    createMachineType$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.CreateMachineType),
        withLatestFrom(this.store.pipe(select(selectMachineTypes))),
        mergeMap(([action, types]: any[]) => this.machineService.createMachineType(action.payload)
            .pipe(
                map((type: IMachineType) => {
                    const message = {user: ToastrUsersType.MACHINE_TYPE, type: ToastrMessageType.CREATE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types.push(type);
                    }
                    return new fromMachine.LoadedMachineTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            ))
    );

    @Effect()
    updateMachineType$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.UpdateMachineType),
        withLatestFrom(this.store.pipe(select(selectMachineTypes))),
        mergeMap(([action, types]: any[]) => this.machineService.updateMachineType(action.payload)
            .pipe(
                map((type: IMachineType) => {
                    const message = {user: ToastrUsersType.MACHINE_TYPE, type: ToastrMessageType.UPDATE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types = types.map((item: IMachineType) => item.id === type.id ? type : item);
                    }
                    return new fromMachine.LoadedMachineTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            ))
    );

    @Effect()
    deleteMachineType$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.DeleteMachineType),
        withLatestFrom(this.store.pipe(select(selectMachineTypes))),
        mergeMap(([action, types]: any[]) => this.machineService.deleteMachineType(action.payload)
            .pipe(
                map((type: IMachineType) => {
                    const message = {user: ToastrUsersType.MACHINE_TYPE, type: ToastrMessageType.REMOVE};
                    if (type) {
                        this.messageService.showToastrSuccess(message);
                        types = types.filter((item: IMachineType) => item.id !== action.payload);
                    }
                    return new fromMachine.LoadedMachineTypes(types);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            ))
    );

    @Effect()
    loadMachines$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.LoadMachines),
        mergeMap(() => this.machineService.getMachines()
            .pipe(
                map((machines: IMachine[]) => new fromMachine.LoadedMachines(machines)),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            )
        )
    );

    @Effect()
    createMachine$ = this.actions$
        .pipe(
            ofType(fromMachine.ActionTypes.CreateMachine),
            withLatestFrom(this.store.select(selectMachinesData)),
            switchMap(([action, machines]: any[]) => this.machineService.createMachine(action.payload)
                .pipe(
                    map((machine: IMachine) => {
                        const isUpdated = machines.find((item: IMachine) => item.id === machine.id);
                        if (isUpdated) {
                            const message = {user: ToastrUsersType.MACHINE, type: ToastrMessageType.UPDATE};
                            machines = machines.map((item: ICommunication) =>
                                item.id === machine.id ? machine : item);
                            this.messageService.showToastrSuccess(message);
                        } else {
                            const message = {user: ToastrUsersType.MACHINE, type: ToastrMessageType.CREATE};
                            machines.push(machine);
                            this.messageService.showToastrSuccess(message);
                        }

                        return new fromMachine.LoadedMachines(machines);
                    }),
                    catchError((err) => {
                        this.messageService.showPushNotification(err);
                        this.messageService.showToastrError(err);
                        return of(new fromMachine.MachineErrors(err));
                    })
                )
            )
        );

    @Effect()
    deleteMachine$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.DeleteMachine),
        withLatestFrom(this.store.pipe(select(selectMachinesData))),
        mergeMap(([action, machines]: any[]) => this.machineService.deleteMachine(action.payload)
            .pipe(
                map(() => {
                    const message = {user: ToastrUsersType.MACHINE, type: ToastrMessageType.REMOVE};
                    this.messageService.showToastrSuccess(message);
                    machines = machines.filter((item: IMachine) => item.id !== action.payload);
                    return new fromMachine.LoadedMachines(machines);
                }),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            ))
    );

    @Effect()
    linkCommunication$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.LinkMachine),
        mergeMap((action: any) => {
            const {machineId, personId} = action.payload;
            return this.machineService.linkMachine(machineId, personId).pipe(
                map(() => new fromMachine.LoadMachines()),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            );
        })
    );

    @Effect()
    unlinkCommunication$ = this.actions$.pipe(
        ofType(fromMachine.ActionTypes.UnlinkMachine),
        mergeMap((action: any) => this.machineService.unlinkMachine(action.payload)
            .pipe(
                map(() => new fromMachine.LoadMachines()),
                catchError((err) => {
                    this.messageService.showPushNotification(err);
                    this.messageService.showToastrError(err);
                    return of(new fromMachine.MachineErrors(err));
                })
            )
        )
    );
}
