import {Component, OnInit, ViewChild} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/internal/operators/tap';
import {map} from 'rxjs/operators';
import {ActivatedRoute} from '@angular/router';
import {NgSelectComponent} from '@ng-select/ng-select';

import {ICourse} from '@models/interfaces/courses';
import {IPerson} from '@models/interfaces/human';
import {IDivision} from '@models/interfaces/division';
import {LoadDivisions} from '@divisions/actions/actions';
import {LoadPersonsByDivisionApi} from '@persons/actions/actions';
import {CreateCourse, DeleteCourse, LoadCoursesByDivision, LoadCoursesByPerson, UpdateCourse} from '@course/actions/actions';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {ModalService} from '@modal/services/modal.service';
import {UnsubscribeOndestroyAdapter} from '../../../adstracts/unsubscribe-ondestroy-adapter';
import {PageTypes} from '@models/constants/page-types';
import {Actions} from '@models/constants/actions';
import {Animations} from '@models/constants/animations';
import {CourseMocks} from '@models/mocks/course.mocks';
import {selectCourseData, selectCoursesCount} from '@course/selectors/selectors';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {selectPersonsByDivisionApi} from '@persons/selectors/selectors';

@Component({
    selector: 'app-course',
    templateUrl: './course.component.html',
    styleUrls: ['./course.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class CourseComponent extends UnsubscribeOndestroyAdapter implements OnInit {

    public pageTypes = PageTypes;
    public actions: string[];
    public selectedDivision: IDivision;
    public selectedPerson: IPerson;
    public divisions: IDivision[];
    public persons: IPerson[];
    public page: number;
    public size: number;
    public coursesCount: number;

    public courses$: Observable<ICourse[]>;
    public divisions$: Observable<IDivision[]>;
    public personsByDivision$: Observable<IPerson[]>;
    public coursesCount$: Observable<number>;

    @ViewChild(NgSelectComponent, {static: false}) ngSelect: NgSelectComponent;

    constructor(private store: Store<any>,
                private modalService: ModalService,
                private route: ActivatedRoute) {
        super();
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE];
        this.getRouteParams();
        this.getDivisions();
    }

    getRouteParams() {
        this.subscriptions.sink = this.route.queryParamMap
            .pipe(map((params) => {
                this.page = Number(params.get('page'));
                this.size = Number(params.get('size'));
            })).subscribe(() => {
                this.getCourses();
                this.getCoursesCount();
            })
        ;
    }

    getCoursesCount() {
        this.coursesCount$ = this.store.pipe(
            select(selectCoursesCount),
            tap((coursesCount: number) => {
                if (coursesCount) {
                    this.coursesCount = coursesCount;
                }
            })
        );
    }

    getDivisions() {
        this.divisions$ = this.store.select(selectDivisionsData)
            .pipe(tap((divisions: IDivision[]) => {
                if (!divisions) {
                    return this.fetchDivisions();
                }
                this.divisions = divisions;
                setTimeout(() => this.selectDefaultDivision(this.divisions));
            }));
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    selectDefaultDivision(divisions: IDivision[]) {
        const item = this.ngSelect.itemsList.findByLabel(divisions[0].name);
        this.ngSelect.select(item);
        this.getPersons(divisions[0].id);
    }

    getPersons(divisionId: string) {
        this.store.dispatch(new LoadPersonsByDivisionApi(divisionId));
        this.personsByDivision$ = this.store.pipe(select(selectPersonsByDivisionApi));
    }

    getCourses() {
        this.courses$ = this.store.select(selectCourseData);
    }

    add() {
        this.openCourseModal(CourseMocks.emptyCourse, false);
    }

    handleActions(event: { action: string, item: ICourse }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openCourseModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
        }
    }

    openCourseModal(course: ICourse, edit: boolean) {
        const objectType = this.pageTypes.COURSE;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...course}, edit, objectType},
            (res) => {
                if (res) {
                    edit ? this.updateCourse(res, course.id, course.person.id) : this.createCourse(res);
                }
            });
    }

    openDialogModal(message: string, item: ICourse) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.name, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteCourse(item.id);
                }
            });
    }

    createCourse(course: ICourse) {
        course['person'] = this.selectedPerson.id;
        this.store.dispatch(new CreateCourse(course));
    }

    updateCourse(updatedCourse: ICourse, courseId: string, personId: string) {
        updatedCourse['id'] = courseId;
        updatedCourse['person'] = personId;
        this.store.dispatch(new UpdateCourse(updatedCourse));
    }

    deleteCourse(courseId: string) {
        this.store.dispatch(new DeleteCourse(courseId));
    }

    onSelectItem(value: any, control: string) {
        switch (control) {
            case 'divisions':
                this.selectedDivision = value;
                this.fetchCoursesByDivision(this.selectedDivision.id, this.pageSettings);
                this.getPersons(this.selectedDivision.id);
                return;
            case 'persons':
                this.selectedPerson = value;
                if (!this.selectedPerson) {
                    return this.fetchCoursesByDivision(this.selectedDivision.id, this.pageSettings);
                }
                this.fetchCoursesByPerson(this.selectedPerson.id, {pageNumber: 1, pageSize: 10});
                return;
        }
    }

    fetchCoursesByDivision(divisionId: string, page: { pageNumber: number, pageSize: number }) {
        const {pageNumber, pageSize} = page;
        this.store.dispatch(new LoadCoursesByDivision({pageNumber, pageSize, divisionId}));
    }

    fetchCoursesByPerson(personId: string, page: { pageNumber: number, pageSize: number }) {
        const {pageNumber, pageSize} = page;
        this.store.dispatch(new LoadCoursesByPerson({pageNumber, pageSize, personId}));
    }

    get pageSettings(): { pageNumber: number, pageSize: number } {
        return {pageNumber: this.page, pageSize: this.size};
    }

}
