import {Component, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';

import {Animations} from '@models/constants/animations';
import {CommunicationMocks} from '@models/mocks/communication';
import {ICommunication} from '@models/interfaces/communication';
import {BaseModalComponent} from '@modal/components/base-modal/base-modal.component';
import {DialogModalComponent} from '@modal/components/dialog-modal/dialog-modal.component';
import {PageTypes} from '@models/constants/page-types';
import {ModalService} from '@modal/services/modal.service';
import {Actions} from '@models/constants/actions';
import {
  CreateCommunication,
  DeleteCommunication,
  LinkCommunication,
  LoadCommunications,
  LoadCommunicationTypes,
  UnlinkCommunication
} from '@communication/actions/actions';
import {Observable} from 'rxjs';
import {selectCommunicationData} from '@communication/selectors/selectors';
import {tap} from 'rxjs/operators';
import {WeaponModalComponent} from '@modal/components/weapon-modal/weapon-modal.component';

@Component({
    selector: 'app-communication',
    templateUrl: './communication.component.html',
    styleUrls: ['./communication.component.scss'],
    animations: [Animations.enterLeaveOpacity]
})
export class CommunicationComponent implements OnInit {
    public pageTypes = PageTypes;
    public actions: string[];

    public communications$: Observable<ICommunication[]>;

    constructor(private modalService: ModalService,
                private store: Store<any>) {
    }

    ngOnInit() {
        this.actions = [Actions.CHANGE, Actions.REMOVE, Actions.TIE, Actions.UNTIE];
        this.getCommunications();
        this.getCommunicationTypes();
    }

    getCommunications() {
        this.communications$ = this.store.pipe(
            select(selectCommunicationData),
            tap((communications: ICommunication[]) => !communications && this.fetchCommunications())
        );
    }

    fetchCommunications() {
        this.store.dispatch(new LoadCommunications());
    }

    getCommunicationTypes() {
        this.store.dispatch(new LoadCommunicationTypes());
    }

    add() {
        this.openCommunicationModal(CommunicationMocks.emptyCommunication, false);
    }

    handleActions(event: { action: string, item: ICommunication }) {
        switch (event.action) {
            case Actions.CHANGE:
                return this.openCommunicationModal(event.item, true);
            case Actions.REMOVE:
                return this.openDialogModal('dialogQuestion', event.item);
            case Actions.TIE:
                return this.openLinkModal(event.item.id, {owner: undefined}, true);
            case Actions.UNTIE:
                return this.unlinkCommunication(event.item.id);
        }
    }

    openCommunicationModal(communication: ICommunication, edit: boolean) {
        const objectType = this.pageTypes.COMMUNICATION;
        this.modalService.openModal(BaseModalComponent,
            {centered: true},
            {object: {...communication}, edit, objectType},
            (res) => {
                if (res) {
                    if (edit) {
                        const body = {...communication, ...res};
                        this.createCommunication(body);
                    } else {
                        this.createCommunication(res);
                    }
                }
            });
    }

    openDialogModal(message: string, item: ICommunication) {
        this.modalService.openModal(DialogModalComponent,
            {centered: true},
            {itemName: item.serialNumber, message},
            (res) => {
                if (res && res === 'Yes') {
                    this.deleteCommunication(item.id);
                }
            });
    }

    openLinkModal(communicationId: string, object: any, isLink: boolean) {
        this.modalService.openModal(WeaponModalComponent,
            {centered: true},
            {object, isLink},
            (res) => {
                if (res) {
                    this.linkCommunication(communicationId, res.owner.id);
                }
            }
        );
    }

    createCommunication(communication: ICommunication) {
        this.store.dispatch(new CreateCommunication(communication));
    }

    deleteCommunication(communicationId: string) {
        this.store.dispatch(new DeleteCommunication(communicationId));
    }

    linkCommunication(communicationId: string, personId: string) {
        this.store.dispatch(new LinkCommunication({communicationId, personId}));
    }

    unlinkCommunication(communicationId: string) {
        this.store.dispatch(new UnlinkCommunication(communicationId));
    }

}
