import {Component, OnInit} from '@angular/core';
import {select, Store} from '@ngrx/store';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

import {IWorkSpace} from '@models/interfaces/workspace';
import {ISubject, ITest} from '@models/interfaces/quiz';
import {IDivision} from '@models/interfaces/division';
import {IPerson} from '@models/interfaces/human';
import {PageTypes} from '@models/constants/page-types';
import {Selects} from '@models/constants/actions';
import {CheckWorkspaces, LoadWorkspaces, StartWorkspaces} from '@workspace/actions/actions';
import {LoadSubjects, LoadTestsBySubject} from '@quiz/actions/actions';
import {LoadDivisions} from '@divisions/actions/actions';
import {LoadPersons, LoadPersonsByDivision} from '@persons/actions/actions';
import {selectWorkspaceData} from '@workspace/selectors/selectors';
import {selectSubjects, selectSubjectTests} from '@quiz/selectors/selectors';
import {selectDivisionsData} from '@divisions/selectors/selectors';
import {selectPersonData, selectPersonsByDivision} from '@persons/selectors/selectors';

@Component({
    selector: 'app-workspace',
    templateUrl: './workspace.component.html',
    styleUrls: ['./workspace.component.scss']
})
export class WorkspaceComponent implements OnInit {
    public pageTypes = PageTypes;
    public selects: string[];
    public workspaces$: Observable<IWorkSpace[]>;
    public subjects$: Observable<ISubject[]>;
    public tests$: Observable<ITest[]>;
    public divisions$: Observable<IDivision[]>;
    public persons$: Observable<IPerson[]>;
    public personsByDivision$: Observable<IPerson[]>;
    public currentSubject: string;
    public currentTest: string;
    public currentStudent: string;
    public currentDivision: string;

    constructor(private store: Store<any>) {
    }

    ngOnInit() {
        this.selects = [Selects.SUBJECT, Selects.TEST, Selects.STUDENT];
        this.getWorkspaces();
        this.getSubjects();
        this.getDivisions();
        this.getPersons();
    }

    getDivisions() {
        this.divisions$ = this.store.pipe(select(selectDivisionsData),
            tap((divisions) => !divisions && this.fetchDivisions()));
    }

    fetchDivisions() {
        this.store.dispatch(new LoadDivisions());
    }

    getWorkspaces() {
        this.fetchWorkspace();
        this.workspaces$ = this.store.pipe(select(selectWorkspaceData));
    }

    fetchWorkspace() {
        this.store.dispatch(new LoadWorkspaces());
    }

    getSubjects() {
        this.subjects$ = this.store.pipe(
            select(selectSubjects),
            tap((subjects: ISubject[]) => !subjects && this.fetchSubjects()));
    }

    fetchSubjects() {
        this.store.dispatch(new LoadSubjects());
    }

    getTestsBySubject(subject: ISubject) {
        this.currentSubject = subject.id;
        this.fetchTests(subject.id);
        this.tests$ = this.store.pipe(select(selectSubjectTests));
    }

    fetchTests(subjectId: string) {
        this.store.dispatch(new LoadTestsBySubject(subjectId));
    }

    getPersons() {
        this.persons$ = this.store.pipe(select(selectPersonData),
            tap((persons: IPerson[]) => !persons && this.fetchPersons()));
    }

    fetchPersons() {
        this.store.dispatch(new LoadPersons());
    }

    getPersonsByDivision() {
        this.personsByDivision$ = this.store.pipe(select(selectPersonsByDivision));
    }

    getCurrentTest(test: ITest) {
        this.currentTest = test.id;
    }

    getCurrentStudent(student: IPerson) {
        this.currentStudent = student.id;
    }

    onCheckWorkspaces(value: IWorkSpace[]) {
        this.store.dispatch(new CheckWorkspaces(value));
    }

    onStartWorkspaces(value: IWorkSpace[]) {
        this.store.dispatch(new StartWorkspaces(value));
    }

    onSelectItem(value: any) {
        this.currentDivision = value.id;
        this.store.dispatch(new LoadPersonsByDivision(value.id));
        this.getPersonsByDivision();
    }
}
